$image_dir: '../../static/img/';
$fonts_dir: '../assets/fonts/';

$color-black: #000;
$color-crow: #201c27;
$color-white: #fff;
$color-gray: #4f4f4f;
$color-silver: #828282;
$red: #ff4930;

$f-semibold: 'Conv_manrope-semibold';
$f-medium: 'Conv_manrope-medium';
$f-regular: 'Conv_manrope-regular';
$f-light: 'Conv_Manrope-Light';
$f-bold: 'Conv_manrope-bold';

@mixin fontz($f, $fz, $lh) {
  font-family: $f;
  font-size: $fz;
  line-height: $lh;
}
@mixin fzlh($fz, $lh) {
  font-size: $fz;
  line-height: $lh;
}
