@import 'tpl.scss';
@import 'fonts.scss';

/* CSS Document */
html {
  height: 100%;
  font-family: $f-semibold;
  font-weight: unset;
  background: #fff url($image_dir + 'bg-header.jpg') no-repeat center top;
  background-size: cover;
}
body {
  height: 100%;
  font-family: $f-semibold;
  color: $color-black;
  font-size: 12px;
  letter-spacing: 0.5px;
}
header,
nav,
section,
article,
aside,
footer {
  display: block;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
img {
  border: none;
}
a {
  outline: none;
  color: #69a7ff;
  &:hover {
    text-decoration: none;
  }
}
input:focus,
textarea:focus {
  outline: none;
}
ul {
  list-style: none;
}
.icon {
  width: 48px;
  height: 48px;
  color: green;
  &:hover {
    color: red;
  }
}
.header {
  position: absolute;
  width: 100%;
  top: 73px;
  left: 0;
  z-index: 50;
  &.sticky {
    position: fixed;
    top: 0;
    background: #fff;
    box-shadow: 0px 30px 60px rgba(0, 0, 0, 0.1);
    .logo {
      margin-top: 22px;
    }
    .logo__img {
      background: #fff url($image_dir + 'logo_black.png') no-repeat 0 0;
      overflow: hidden;
      width: 193px;
      height: 44px;
      img {
        position: absolute;
        left: -200px;
      }
    }
    .logo__text {
      color: $color-black;
      border-color: #808080;
    }
    .header__text {
      color: $color-black;
      line-height: 90px;
    }
    .mail {
      margin-top: 22px;
    }
  }
}
.header__top {
  display: flex;
  justify-content: space-between;
  width: 1170px;
  height: 91px;
  margin: 0 auto;
}
.header__text {
  @include fontz($f-semibold, 14px, 46px);
  color: $color-white;
}
.logo {
  display: flex;
  justify-content: space-between;
  width: 390px;
}
.logo__text {
  @include fontz($f-regular, 12px, 16px);
  height: 40px;
  color: $color-white;
  padding: 4px 0 0 10px;
  border-left: 1px solid #fff;
  margin: 4px 0 0;
}
.mail {
  a {
    @include fontz($f-semibold, 14px, 46px);
    display: block;
    width: 189px;
    height: 46px;
    background: linear-gradient(270deg, #ff6230 0%, #ff3030 100%);
    border-radius: 8px;
    text-align: center;
    text-transform: uppercase;
    color: $color-white;
    text-decoration: none;
    &:hover {
      background: linear-gradient(270deg, #ff6230 0%, #ff3030 70%);
    }
    &:before {
      content: '';
      display: inline-block;
      width: 20px;
      height: 16px;
      margin: -1px 10px 0 0;
      vertical-align: middle;
    }
  }
}
.nav__mobile,
.header__burger,
.accordion__nav,
.send__files_button,
.close__form {
  display: none;
}
.header__bot {
  display: none;
  width: 1170px;
  margin: 0 auto;
  &.act {
    display: block;
  }
  .menu {
    ul {
      display: flex;
      justify-content: space-between;
      padding: 0 24px 0;
      border-top: 1px solid #e0e0e0;
    }
    li {
      span {
        @include fontz($f-semibold, 14px, 16px);
        display: flex;
        align-items: center;
        color: $color-silver;
        text-align: center;
        text-transform: uppercase;
        height: 60px;
        width: 101px;
        cursor: pointer;
        border-bottom: 4px solid #fff;
        transition: all 0.3s ease;
        user-select: none;
        &.active {
          border-color: $red;
          color: $color-black;
          cursor: default;
          caret-color: transparent;
        }
      }
      &:hover {
        span {
          border-color: $red;
          color: $color-black;
        }
      }
    }
  }
}
.head-info {
  position: absolute;
  left: 50%;
  top: 280px;
  transform: translate(-50%, 0);
  width: 1100px;
  margin: 0 auto;
  color: $color-white;
  text-align: center;
  &__title {
    @include fontz($f-bold, 48px, 66px);
    text-transform: uppercase;
  }
  &__text {
    @include fontz($f-regular, 36px, 50px);
    margin: 24px 0 0;
  }
}
.header__link {
  font-family: $f-semibold;
  text-transform: uppercase;
  font-size: 16px;
}
.title {
  @include fontz($f-bold, 36px, 49px);
  text-align: center;
  text-transform: uppercase;
  color: $color-black;
}
.subtitle {
  @include fontz($f-medium, 18px, 25px);
  font-weight: unset;
  text-align: center;
  color: $color-gray;
  position: relative;
  padding: 15px 0 39px;
  &:after {
    content: '';
    position: absolute;
    width: 50px;
    height: 6px;
    bottom: 0;
    left: calc(50% - 50px / 2);
    background: linear-gradient(0deg, $red, $red), #c4c4c4;
  }
}
.files {
  width: 1170px;
  position: relative;
  margin: 627px auto 123px;
  background-color: #fff;
  box-shadow: 0px 30px 60px rgba(0, 0, 0, 0.1);
}
#files__anchor {
  position: absolute;
  left: 0;
  top: -80px;
}
.files__header {
  ul {
    display: flex;
    justify-content: space-between;
    padding: 26px 24px 0;
    border-bottom: 1px solid #e0e0e0;
  }
  li {
    @include fontz($f-semibold, 12px, 16px);
    display: block;
    color: $color-silver;
    text-align: center;
    text-transform: uppercase;
    height: 104px;
    width: 101px;
    cursor: pointer;
    border-bottom: 4px solid #fff;
    transition: all 0.3s ease;
    user-select: none;
    &.active {
      border-color: $red;
      color: $color-black;
      cursor: default;
    }
    &:hover {
      border-color: $red;
      color: $color-black;
    }
    &:before {
      content: '';
      display: block;
      width: 44px;
      height: 48px;
      margin: 0 auto 10px;
    }
  }
}
.arrow {
  display: none;
}
.files__body {
  padding: 40px;
  &_row {
    display: flex;
    justify-content: space-between;
    &.tab-pane {
      display: none;
      &.active {
        display: flex;
      }
    }
  }
}
.files__info {
  width: 57.3%;
  h2 {
    @include fontz($f-semibold, 24px, 33px);
    font-weight: normal;
    color: $color-black;
    margin: 0 0 24px;
  }
  strong,
  b {
    font-family: $f-semibold;
    font-weight: normal;
    color: $color-black;
  }
  ol {
    list-style-type: decimal-leading-zero;
    padding: 0 0 26px 40px;
    li {
      @include fontz($f-regular, 14px, 24px);
      color: $color-gray;
      padding: 0 0 22px 10px;
      ::marker {
        @include fontz($f-semibold, 24px, 22px);
        color: $red;
      }
    }
  }
  p {
    @include fontz($f-regular, 14px, 24px);
    color: $color-black;
  }
  .list__groop {
    @include fontz($f-regular, 14px, 24px);
    color: $color-black;
    ul {
      display: inline-block;
      padding-left: 6px;
      li {
        display: inline-block;
      }
      li + li {
        &:before {
          content: '->';
          color: $red;
          display: inline-block;
          margin: 0 3px 0 0;
        }
      }
    }
  }
}
.files__form {
  width: 39%;
}
.orange {
  color: $red;
}
.form__bg {
  background: #f2f2f2;
  padding: 28px 28px 35px 28px;
}
.form__title {
  @include fontz($f-semibold, 18px, 25px);
  color: $color-black;
  margin: 0 0 22px;
}
.form__drag {
  display: flex;
  align-items: center;
  background: #fff;
  border: 2px dashed #bdbdbd;
  box-sizing: border-box;
  border-radius: 10px;
  height: 90px;
  width: 100%;
  background-position: 30px 25px;
  background-size: 36px 40px;
  background-repeat: no-repeat;
  margin: 0 0 10px;
}
.drag__clip {
  @include fontz($f-regular, 14px, 22px);
  align-items: center;
  color: $color-silver;
  width: 254px;
  margin-left: 94px;
}
.form__input {
  margin: 0 0 2px;
}
.input__label {
  @include fontz($f-semibold, 12px, 24px);
  color: $color-crow;
  display: block;
  margin: 0 0 4px;
}
.input__text {
  font-family: $f-regular;
  font-size: 12px;
  color: $color-crow;
  width: 100%;
  height: 46px;
  border: 1px solid #d4d1da;
  border-radius: 8px;
  background: #fff;
  padding: 0 15px;
}
.form__textarea {
  @include fontz($f-regular, 12px, 20px);
  color: $color-crow;
  width: 100%;
  height: 100px;
  border: 1px solid #d4d1da;
  border-radius: 8px;
  background: #fff;
  padding: 5px 15px;
  resize: none;
}
.form__butt {
  font-family: $f-semibold;
  color: $color-white;
  text-transform: uppercase;
  background: linear-gradient(246.53deg, #2ecdff 30.28%, #71a1ff 100%), #7a4ff2;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  width: 100%;
  &:hover {
    background: linear-gradient(246.53deg, #2ecdff 30.28%, #71a1ff 70%), #7a4ff2;
  }
}
.capcha {
  &__field {
    display: flex;
    align-items: center;
    text-align: center;
    background: #e0e0e0;
    border-radius: 10px;
    height: 94px;
    width: 100%;
    margin: 20px 0 21px;
    &:after {
      content: 'CAPCHA';
      @include fontz($f-light, 36px, 49px);
      color: #9e9e9e;
      width: 100%;
    }
  }
  &__verification {
    display: flex;
    justify-content: space-between;
  }
  &__text {
    width: 66.4%;
  }
  &__butt {
    width: 30%;
  }
}
.team {
  width: 1170px;
  margin: 0 auto;
  &__body {
    margin: 50px 0 62px;
    display: flex;
    justify-content: space-between;
    overflow: hidden;
  }
  &__column {
    position: relative;
    width: 570px;
    height: 298px;
    background: #f2f2f2;
    text-align: center;
    &:before {
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      height: 42px;
      width: 42px;
      background: $red;
    }
    &:after {
      content: '';
      position: absolute;
      right: -30px;
      top: -30px;
      height: 60px;
      width: 60px;
      background: #fff;
      transform: rotate(45deg);
      -ms-transform: rotate(90deg);
    }
  }
  &__title {
    @include fontz($f-bold, 18px, 25px);
    font-weight: inherit;
    color: $color-black;
    text-transform: uppercase;
    margin: 50px 0 28px;
  }
  &__row {
    padding: 0 5px;
    width: 33.333%;
  }
  &__numbers {
    @include fontz($f-light, 64px, 87px);
    color: $red;
    text-transform: uppercase;
  }
  &__text {
    @include fontz($f-regular, 18px, 25px);
    color: $color-black;
  }
}
.column__section {
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
}
.deliver {
  width: 1170px;
  margin: 0 auto 100px;
  &__body {
    margin: 50px 0 44px;
    display: flex;
    justify-content: space-between;
    border-top: 1px solid #e0e0e0;
    border-left: 1px solid #e0e0e0;
  }
  &__body_row {
    width: 25%;
    height: 231px;
    padding: 44px 16px 0;
    border-right: 1px solid #e0e0e0;
    border-bottom: 1px solid #e0e0e0;
  }
  &__numbers {
    @include fontz($f-light, 36px, 49px);
    text-align: center;
    text-transform: uppercase;
    color: $red;
  }
  &__title {
    @include fontz($f-semibold, 18px, 25px);
    text-align: center;
    text-transform: uppercase;
    color: $color-black;
  }
  &__text {
    @include fontz($f-regular, 14px, 19px);
    margin: 26px 0 0;
    text-align: center;
    color: $color-gray;
  }
  &__footer {
    @include fontz($f-semibold, 24px, 33px);
    margin: 44px 0 0;
    align-items: center;
    text-align: center;
    color: $color-black;
  }
}
.technologies {
  position: relative;
  height: 476px;
  width: 1170px;
  margin: 0 auto 73px;
  display: flex;
  justify-content: flex-end;
  padding: 72px 0 0;
  &:before {
    content: '';
    position: absolute;
    left: 0;
    top: 47px;
    width: 519px;
    height: 315px;
    background: url($image_dir + 'technologies.png') no-repeat 0 0;
  }
  &__row {
    width: 50%;
  }
  &__title {
    text-align: left;
  }
  &__subtitle {
    @include fontz($f-regular, 14px, 24px);
    text-align: left;
    margin: 32px 0 15px;
    padding: 29px 0 0;
    &:after {
      bottom: auto;
      top: 0;
      left: 0;
      background: linear-gradient(0deg, $red, $red), #c4c4c4;
    }
  }
}
.about {
  width: 1170px;
  margin: 0 auto 37px;
  &__row {
    display: flex;
    justify-content: space-between;
  }
}
.item {
  &-about {
    width: 370px;
    height: 415px;
    padding: 63px 0 0;
    text-align: center;
    &__photo {
      margin: 0 auto 30px;
      width: 230px;
      height: 230px;
      overflow: hidden;
      border-radius: 50%;
      position: relative;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
        position: absolute;
        left: 0;
        top: 0;
      }
    }
    &__name {
      @include fontz($f-semibold, 24px, 24px);
      margin: 0 0 14px;
    }
    &__position {
      @include fontz($f-regular, 18px, 18px);
      color: $color-gray;
    }
  }
}
.superpower {
  border: 1px solid #e0e0e0;
  width: 906px;
  margin: 0 auto 77px;
  padding: 35px 0 0;
  height: 288px;
  &__row {
    width: 1170px;
    height: 218px;
    display: flex;
    justify-content: space-between;
    margin-left: -132px;
    background: #fff;
    text-align: center;
  }
}
.item-superpower {
  width: 25%;
  padding: 25px 35px 0;
  &__icon {
    display: block;
    margin: 0 auto 18px;
  }
  &__title {
    @include fontz($f-regular, 18px, 20px);
    color: $color-gray;
    margin: 0 0 20px;
  }
  &__text {
    @include fontz($f-semibold, 18px, 25px);
    color: $color-black;
    a {
      color: inherit;
      text-decoration: none;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}
.api-integrations {
  background: url($image_dir + 'api.png') no-repeat center 0;
  padding: 373px 0 65px;
}
.request {
  background: #f2f2f2;
  padding: 70px 0 0;
  &__form {
    display: flex;
    justify-content: space-between;
    width: 770px;
    margin: 0 auto 71px;
  }
  &__subtitle {
    padding-bottom: 56px;
    &:after {
      display: none;
    }
  }
  &__form_drag {
    width: 48%;
    .form__drag {
      height: 470px;
      text-align: center;
      background-position: 158px 150px;
      background-size: 54px 60px;
    }
    .drag__clip {
      @include fzlh(18px, 30px);
      margin: 80px 0 0;
      width: 100%;
      padding: 0 23px;
    }
  }
  .files__form {
    width: 48%;
    padding: 0;
  }
  .capcha__field {
    margin: 22px 0 26px 0;
  }
}
.copyright {
  @include fontz($f-regular, 12px, 16px);
  color: $color-silver;
  padding: 0 0 62px;
  text-align: center;
  a {
    color: $color-silver;
    text-decoration: none;
  }
  &:hover {
    text-decoration: underline;
  }
}

@media screen and (max-width: 1189px) {
  html {
    background-position: center -360px;
  }
  .header {
    top: 34px;
    &__top {
      width: 768px;
      padding: 0 15px;
      position: relative;
    }
    &__bot {
      &.act {
        display: none;
      }
    }
    &__text {
      @include fzlh(11px, 14px);
      height: 40px;
      width: 192px;
      text-align: center;
    }
    &.sticky {
      height: 46px;
      padding: 0;
      .header__text {
        line-height: 14px;
        margin: 9px 0 0;
      }
      .mail {
        margin: 0;
        a {
          border-radius: 0;
        }
      }
      .logo {
        margin: 9px 0 0;
        &__img {
          background: url($image_dir + 'logo_web.png') no-repeat 0 bottom;
          width: 117px;
          height: 27px;
        }
      }
    }
  }
  .logo {
    width: 288px;
    margin: 0;
    &__img {
      background: url($image_dir + 'logo_web.png') no-repeat 0 0;
      width: 117px;
      height: 27px;
      overflow: hidden;
      img {
        display: none;
      }
    }
    &__text {
      @include fzlh(11px, 15px);
      height: 27px;
      padding: 0 0 0 12px;
      margin: 0;
    }
  }
  .mail {
    margin-top: -10px;
    a {
      width: 46px;
      &:before {
        margin: -1px 0 0;
      }
    }
    span {
      display: none;
    }
  }
  .head-info {
    top: 105px;
    width: 800px;
    &__title {
      @include fzlh(36px, 49px);
    }
    &__text {
      @include fzlh(24px, 33px);
      margin: 10px 0 0;
    }
  }
  .files {
    width: 738px;
    margin: 344px auto 54px;
    &__header {
      ul {
        overflow: hidden;
        width: 1236px;
      }
    }
    &__body {
      padding: 15px;
    }
    &__info {
      width: 49.6%;
      h2 {
        @include fzlh(18px, 25px);
      }
      ol {
        padding: 0 0 6px 40px;
        li {
          @include fzlh(12px, 24px);
        }
      }
      .list__groop {
        @include fzlh(12px, 22px);
        ul {
          padding-left: 0;
        }
        strong {
          display: block;
        }
      }
    }
    &__form {
      width: 47.8%;
    }
    &__slide {
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid #e0e0e0;
      ul {
        border: none;
      }
      &_row {
        overflow: hidden;
      }
      &.act {
        ul {
          margin-left: -100%;
          padding-left: 40px;
        }
        .arrow.arrow__left {
          display: block;
          margin-right: 16px;
        }
        .arrow.arrow__right {
          display: none;
        }
      }
    }
  }
  .arrow {
    @include fontz($f-semibold, 12px, 16px);
    display: block;
    text-transform: uppercase;
    color: $color-silver;
    margin-top: 26px;
    cursor: pointer;
    text-align: center;
    transition: all 0.3s ease;
    &.arrow__left {
      margin-left: 50px;
      display: none;
      user-select: none;
    }
    &.arrow__right {
      margin-right: 50px;
      user-select: none;
    }
    &:hover {
      color: $color-black;
    }
    &:before {
      display: block;
      width: 48px;
      height: 48px;
      content: '';
      border-radius: 50%;
      margin-bottom: 10px;
    }
  }
  .form__drag {
    height: 113px;
    background-position: center 20px;
    background-size: 36px 40px;
  }
  .drag__clip {
    span {
      display: none;
    }
    a {
      margin: 46px 0 0;
      display: block;
    }
  }
  .title {
    @include fzlh(24px, 33px);
  }
  .subtitle {
    @include fzlh(14px, 19px);
    padding: 10px 0 26px;
    &:after {
      width: 32px;
    }
  }
  .team {
    width: 708px;
    &__body {
      margin: 39px 0 46px;
    }
    &__column {
      width: 344px;
      height: 180px;
      &:before {
        width: 25px;
        height: 25px;
      }
      &:after {
        right: -22px;
        top: -22px;
        height: 40px;
        width: 40px;
      }
    }
    &__row {
      padding: 0 2px;
    }
    &__title {
      @include fzlh(14px, 19px);
      margin: 23px 0 12px;
    }
    &__numbers {
      @include fzlh(36px, 49px);
      height: 56px;
    }
    &__text {
      @include fzlh(14px, 19px);
    }
  }
  .column__section {
    padding: 0 5px;
  }
  .deliver {
    width: 738px;
    margin: 0 auto 48px;
    &__body {
      margin: 37px 0 29px;
      &_row {
        padding: 28px 15px 0;
        height: 146px;
      }
    }
    &__numbers {
      @include fzlh(24px, 33px);
    }
    &__title {
      @include fzlh(14px, 19px);
    }
    &__text {
      @include fzlh(12px, 16px);
      margin: 13px 0 0;
      letter-spacing: 0;
    }
    &__footer {
      @include fzlh(14px, 19px);
      width: 578px;
      margin: 0 auto;
    }
  }
  .technologies {
    width: 738px;
    height: 310px;
    background-size: 467px 310px;
    padding: 22px 0 0;
    margin: 0 auto 47px;
    &:before {
      width: 325px;
      height: 197px;
      background-size: cover;
    }
    &__title {
      width: 80%;
    }
    &__subtitle {
      padding: 20px 0 15px;
      margin: 24px 0 15px;
    }
  }
  .about {
    width: 750px;
  }
  .item {
    &-about {
      padding: 28px 0 0;
      width: 217px;
      height: 272px;
      &__photo {
        width: 185px;
        height: 185px;
      }
      &__name {
        font-size: 18px;
        margin: 0 0 8px;
      }
      &__position {
        font-size: 14px;
      }
    }
  }
  .superpower {
    border: none;
    padding: 25px 0 0;
    margin: 0 auto 10px;
    width: auto;
    overflow: hidden;
    &__row {
      width: 814px;
      margin: 0 auto;
    }
  }
  .item-superpower {
    &__icon {
      margin: 0 0 26px;
    }
    &__title {
      @include fzlh(14px, 75%);
      margin: 0 0 22px;
    }
    &__text {
      @include fzlh(14px, 19px);
    }
  }
  .api-integrations {
    width: 736px;
    margin: 0 auto;
    padding: 210px 0 54px;
    background-size: 331px 188px;
  }
  .request {
    padding: 55px 0 0;
    &__form {
      flex-direction: column;
      width: 481px;
      margin: 0 auto 54px;
      &_drag {
        width: 100%;
        .form__drag {
          height: 113px;
          background-position: 227px 21px;
        }
      }
    }
    .files__form {
      width: 100%;
    }
  }
  .request__form_drag {
    .drag__clip {
      @include fzlh(14px, 22px);
      margin: 0;
    }
    .form__drag {
      background-size: 36px 40px;
      margin: 0 0 18px;
    }
  }
  .copyright {
    @include fzlh(10px, 14px);
    padding: 0 0 58px;
  }
}

@media screen and (max-width: 840px) {
  html {
    background-size: auto;
  }
  body {
    min-width: 320px;
    overflow-x: hidden;
  }
  body.ov {
    overflow-y: hidden;
  }
  .subtitle:after {
    left: 50%;
    margin-left: -16px;
  }
  .header {
    top: 25px;
    &__top {
      width: 100%;
      height: 48px;
      margin: 0;
      padding: 0 15px;
      min-width: 320px;
    }
    &.sticky {
      .header__top {
        padding: 0;
      }
      .header__burger {
        margin: 14px 0 0 15px;
        &:before,
        &:after,
        span {
          background-color: #000;
        }
      }
    }
  }
  .header__text,
  .logo__text {
    display: none;
  }
  .logo {
    width: auto;
  }
  .header__burger {
    display: block;
    position: relative;
    width: 22px;
    height: 20px;
    margin: 5px 0 0 8px;
    cursor: pointer;
    user-select: none;
    span {
      position: absolute;
      background-color: #fff;
      position: absolute;
      left: 0;
      width: 50%;
      height: 2px;
      top: 9px;
    }
    &:after,
    &:before {
      content: '';
      background-color: #fff;
      position: absolute;
      width: 100%;
      height: 2px;
      left: 0;
    }
    &:before {
      top: 0;
    }
    &:after {
      bottom: 0;
    }
  }
  .head-info {
    width: 100%;
    min-width: 320px;
    padding: 0 15px;
    &__title {
      @include fzlh(24px, 33px);
    }
    &__text {
      @include fzlh(18px, 25px);
    }
  }
  .files {
    margin: 325px 15px 50px;
    box-sizing: border-box;
    width: auto;
    &__body {
      padding: 0;
    }
  }
  .accordion_wrap .files__form,
  .arrow.arrow__right,
  .arrow.arrow__left,
  .files__slide.act .arrow.arrow__left,
  .files__slide.act .arrow.arrow__right {
    display: none;
  }
  .files {
    &__header ul {
      width: 100%;
    }
    &__info {
      width: 100%;
      padding: 15px;
    }
    &__slide_row {
      display: none;
    }
  }
  .head-info {
    top: 120px;
    width: 100%;
    max-width: 637px;
    padding: 0 15px;
  }
  .team {
    width: 100%;
    padding: 0 15px;
    &__column {
      height: 152px;
    }
    &__title {
      margin: 18px 0 8px;
    }
    &__text {
      @include fzlh(12px, 16px);
    }
    &__body {
      flex-direction: column;
      margin: 39px 0 8px;
    }
    &__column {
      position: relative;
      z-index: 2;
      width: 100%;
      margin: 0 auto 16px;
      max-width: 500px;
    }
    &__column + &__column {
      z-index: 1;
    }
  }
  .deliver {
    width: 100%;
    padding: 0 15px;
    margin: 0 auto 43px;
    &__body {
      max-width: 500px;
      margin: 26px auto 29px;
      flex-wrap: wrap;
      &_row {
        width: 50%;
      }
    }
    &__footer {
      width: 100%;
    }
  }
  .technologies {
    padding: 0;
    width: 100%;
    height: auto;
    padding: 200px 15px 0;
    margin: 0 auto 10px;
    &:before {
      display: block;
      top: 0;
      left: 50%;
      margin-left: -137px;
      background-size: 275px 167px;
      background-position: center top;
    }
    &__row {
      width: 100%;
    }
    &__title {
      @include fzlh(24px, 33px);
      width: 100%;
      text-align: center;
      padding: 0 15px;
    }
    &__subtitle {
      @include fzlh(12px, 24px);
      margin: 15px 0 15px;
      padding: 20px 0 0;
    }
  }
  .about {
    width: 100%;
    padding: 0 15px;
    &__row {
      flex-direction: column;
      margin: 20px 0 0;
    }
  }
  .item-about {
    width: 100%;
    height: auto;
    padding: 30px 0 0;
    &__photo {
      margin: 0 auto 20px;
    }
  }
  .superpower {
    width: 100%;
    height: auto;
    padding: 0 15px;
    margin: 30px 0 56px;
    &__row {
      width: 100%;
      height: auto;
      flex-wrap: wrap;
    }
  }
  .item-superpower {
    width: 50%;
    height: 162px;
    padding: 0 4px 0;
    &__title {
      margin: 0 0 8px;
    }
  }
  .api-integrations {
    background: url($image_dir + 'api_mobile.png') no-repeat center 0;
    width: 100%;
    padding: 200px 15px 43px;
  }
  .request {
    padding: 42px 15px 0;
    width: 100%;
    &__form {
      width: 100%;
      margin: 0 auto 27px;
      .subtitle {
        padding: 10px 0 17px;
      }
    }
    &__form_drag {
      .form__drag {
        background-position: center 21px;
      }
    }
  }
  .copyright {
    padding: 0 0 30px;
  }
  .nav__mobile {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    padding: 21px 0 26px;
    z-index: 50;
    &.active {
      display: block;
    }
  }
  .close__nav {
    width: 17px;
    height: 17px;
    cursor: pointer;
    margin-left: 21px;
    user-select: none;
  }
  .nav__mobile {
    min-width: 320px;
    overflow-x: scroll;
    height: 100%;
    &_wrap {
      margin: 26px 0;
      li {
        position: relative;
        display: flex;
        justify-content: space-between;
        padding: 18px 27px;
        line-height: 62px;
        cursor: pointer;
        user-select: none;
        &:before {
          content: '';
          position: absolute;
          left: 21px;
          top: 50%;
          margin-top: -12px;
        }
        span {
          @include fontz($f-semibold, 12px, 16px);
          display: block;
          color: $color-white;
          padding-left: 36px;
          text-transform: uppercase;
        }
      }
    }
  }
  .nav__mobile {
    .mail {
      padding: 0 21px;
      a {
        width: 189px;
        width: 100%;
        background: #7a4ff2;
        span {
          display: inline-block;
        }
      }
      &:before {
        margin: -1px 10px 0 0;
      }
    }
  }

  .files__body_row.tab-pane.active {
    display: none;
  }
  .accordion_wrap {
    position: relative;
    user-select: none;
  }
  .accordion_wrap.active .files__body_row.tab-pane,
  .accordion_wrap.active .files__body_row.tab-pane.active {
    display: block;
  }
  .accordion__nav {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 18px 36px;
    line-height: 62px;
    cursor: pointer;
    text-transform: uppercase;
    border-top: 1px solid #e0e0e0;
    &.no_border {
      border-top: none;
    }
    &:before {
      content: '';
      position: absolute;
      left: 21px;
      top: 50%;
      margin-top: -12px;
    }
    span {
      @include fontz($f-semibold, 12px, 16px);
      display: block;
      color: $color-black;
      padding-left: 20px;
    }
  }
  .accordion__anchor {
    position: absolute;
    height: 0;
    width: 0;
    top: -48px;
    left: 0;
  }
  .send__files_button {
    display: block;
    margin-top: 22px;
    padding: 0;
    a {
      @include fontz($f-semibold, 14px, 46px);
      display: block;
      width: 100%;
      height: 46px;
      border-radius: 8px;
      text-align: center;
      text-transform: uppercase;
      color: $color-white;
      text-decoration: none;
      width: 189px;
      width: 100%;
      background: #7a4ff2;
      span {
        display: inline-block;
      }
      &:before {
        content: '';
        display: inline-block;
        width: 20px;
        height: 20px;
        margin: -1px 10px 0 0;
        vertical-align: middle;
      }
    }
  }
  .open {
    .files__form {
      display: block;
      position: fixed;
      width: 100%;
      min-width: 320px;
      height: 100vh;
      z-index: 50;
      left: 0;
      top: 0;
      background: #f2f2f2;
      .form__bg {
        display: flex;
        width: 100%;
        flex-flow: column;
        height: 100%;
        background: #f2f2f2;
        padding: 0;
        .form__ {
          width: 100%;
          overflow-x: auto;
          padding: 42px 28px 35px 28px;
        }
      }
      .form__title {
        @include fzlh(18px, 25px);
        text-align: center;
        text-transform: uppercase;
      }
      .drag__clip {
        margin: 0;
        width: 100%;
        text-align: center;
      }
      .capcha__text {
        width: 64.5%;
      }
      .capcha__butt {
        width: 32.2%;
      }
    }
    .close__form {
      position: absolute;
      right: 15px;
      top: 15px;
      display: block;
      width: 17px;
      height: 17px;
      cursor: pointer;
    }
  }
}
